//========== Roboto ==========

//Roboto Regular
@include font("Roboto Regular", "roboto-regular");
@mixin roboto-reg {
	font-family: "Roboto Regular", Arial, Helvetica, sans-serif;
	font-weight: normal; }

//Roboto Bold
@include font("Roboto Bold", "roboto-bold");
@mixin roboto-bold {
	font-family: "Roboto Bold", Arial, Helvetica, sans-serif;
	font-weight: normal; }

//Roboto Black
@include font("Roboto Black", "roboto-black");
@mixin roboto-black {
	font-family: "Roboto Black", Arial, Helvetica, sans-serif;
	font-weight: normal; }

//Roboto BlackItalic
@include font("Roboto BlackItalic", "roboto-blackitalic");
@mixin roboto-italic {
	font-family: "Roboto BlackItalic", Arial, Helvetica, sans-serif;
	font-weight: normal; }



//========== Raleway ==========

//Raleway Regular
@include font("Raleway Regular", "raleway-regular");
@mixin raleway-reg {
	font-family: "Raleway Regular", Arial, Helvetica, sans-serif;
	font-weight: normal; }

//Raleway Semibold
@include font("Raleway Semibold", "raleway-semibold");
@mixin raleway-semi {
	font-family: "Raleway Semibold", Arial, Helvetica, sans-serif;
	font-weight: normal; }

//Raleway Bold
@include font("Raleway Bold", raleway-bold);
@mixin raleway-bold {
	font-family: "Raleway Bold", Arial, Helvetica, sans-serif;
	font-weight: normal; }

//Raleway ExtraBold
@include font("Raleway ExtraBold", "raleway-extrabold");
@mixin raleway-extra {
	font-family: "Raleway ExtraBold", Arial, Helvetica, sans-serif;
	font-weight: normal; }
